
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'
import Alert from 'vux/src/components/alert/index.vue'

export default {
components: {
XInput,
XButton,
Group,
Cell,
FormPreview,
Alert
},
data () {
return {
show: false,
message: '',
title:'操作提示',
searchID:'',
playerID:'',
list:[
{
label: 'ID',
value: ''
},{
label: '名称',
value: ''
}, {
label: '房卡数',
value: ''
}],
buttons:[{
style:'primary',
text:'为该玩家充值',
onButtonClick: (name) => {
if(this.playerID == ''){
this.show=true
this.message ="请输入玩家ID"
return
}

this.$router.push('/chargeforplayer/'+this.playerID)

}
}]
}
},
methods:{
checkUser:function(){
if(this.searchID == ''){
this.show=true
this.message ="请输入玩家ID"
return
}
var params = new URLSearchParams()
params.append('userid', this.searchID)
var that = this
this.$http.post('user/checkUser',params)
.then((ret)=>{
console.log(ret);
if(ret.data.err==-1){ //没有这个id
that.show=true
that.message ="请输入正确的ID"
return
}
if (ret.data.err==-2) {//参数错误
that.show=true
that.message ="请输入正确的ID"
return
}
if (ret.data.err==-999) {
that.show=true
that.message ="请先登录！"
setTimeout(()=>{that.$router.push('/login')},2000)
return
}
that.playerID = ret.data.res[0].userid
that.list[0].value=ret.data.res[0].userid
that.list[1].value=ret.data.res[0].name
that.list[2].value=ret.data.res[0].gems
})
}
}
}



