
import Alert from 'vux/src/components/alert/index.vue'

import md5 from 'js-md5';
export default {
data(){
return {
username: '',
password: '',
message: '',
show: false,
title: '',
repass:''
}
},
components:{
Alert
},
methods:{
jumptologin:function(){
this.$router.push('/login')
},
userRegist:function(){
if(!(/^1[3|4|5|7|8][0-9]\d{4,8}$/.test(this.username))){
this.show=true
this.message='请输入正确的手机号'
return false;
}

if(this.password==''||this.username==''){
this.show=true
this.message='帐号/密码不能为空'
return
}
if (this.password.length < 6 || this.password > 18) {
this.show=true
this.message='密码长度为6-18位'
return
}
if(this.password!=this.repass){
this.show=true
this.message='两次密码不一致'
return
}

var params = new URLSearchParams()
params.append('username',this.username)
params.append('password',md5(this.password))
var that=this
this.$http.post('newUser',params)

.then((res)=>{

if(res.data.err==0){
that.show=true
that.message='注册成功，请登录'
setTimeout(()=>{that.$router.push('/login')},3000)

}if (res.data.err==-1) {
that.show=true
that.message='用户名已存在'
}
})
}
}
}



