
function getQueryString(name) {
var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
var r = window.location.search.substr(1).match(reg);
if (r != null) return unescape(r[2]); return null;
}
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Picker from 'vux/src/components/picker/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Alert from 'vux/src/components/alert/index.vue'


export default {
components: {
PopupPicker,
Group,
Picker,
XButton,
Divider,
Cell,
XSwitch,
Alert
},
mounted:function(){
if(!getQueryString("code")){
location.href='https://open.weixin.qq.com/connect/oauth2/authorize?appid=wxedf69f9306bc876a&redirect_uri=http%3a%2f%2fdealer.870yx.com%2fchargeforcards&response_type=code&scope=snsapi_base&state=123#wechat_redirect'

}
},
data () {
return {
list1: [[
'500元 3300张',
'1000元 7000张',
'2000元 15000张'
]],
value1: [],
message: '',
show: false,
title: '',
returndata:'',
code:'code',
res:'res',
totalfee:0,
try:false
}
},
methods: {
onChange (val) {
console.log('val change', val)
},
onShow () {
console.log('on show')
},
onHide (type) {
console.log('on hide', type)
},
wxpay:function(){

if (this.value1=='') {
this.show=true
this.message='请先选择套餐'
return
}
if(this.try){
return
}

if(!getQueryString("code")){
location.href='https://open.weixin.qq.com/connect/oauth2/authorize?appid=wxedf69f9306bc876a&redirect_uri=http%3a%2f%2fdealer.870yx.com%2fchargeforcards&response_type=code&scope=snsapi_base&state=123#wechat_redirect'

}else {




if (parseInt(this.value1[0]) == 500) {
this.totalfee = 50000

}
else if (parseInt(this.value1[0]) == 1000) {
this.totalfee = 100000

}
else if (parseInt(this.value1[0]) == 2000) {
this.totalfee = 200000

}else {
this.show=true
this.message='您的输入有误'
return
}
this.try=!this.try
var params = new URLSearchParams()
params.append('code',getQueryString("code"))
params.append('fee',this.totalfee)

var that = this

this.$http.post('user/getToken',params)
.then((res)=>{
that.try=!that.try
var appId = res.data.appId
var timeStamp = res.data.timeStamp
var nonceStr = res.data.nonceStr
var mypackage = res.data.package
var signType = res.data.signType
var sign = res.data.sign

function onBridgeReady(){
WeixinJSBridge.invoke(
'getBrandWCPayRequest', {
"appId":appId, //公众号名称，由商户传入
"timeStamp":timeStamp,//时间戳，自1970年以来的秒数
"nonceStr":nonceStr, //随机串
"package":mypackage,
"signType":signType,  //微信签名方式：
"paySign":sign//微信签名
},
function(res){

window.location.href='/personal'

}
);

}
if (typeof WeixinJSBridge == "undefined"){
if( document.addEventListener ){
document.addEventListener('WeixinJSBridgeReady', onBridgeReady, false);
}else if (document.attachEvent){
document.attachEvent('WeixinJSBridgeReady', onBridgeReady);
document.attachEvent('onWeixinJSBridgeReady', onBridgeReady);
}
}else{
onBridgeReady();
}
})
}
}
}
}








