
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Alert from 'vux/src/components/alert/index.vue'

import Mycell from '@/mycomponents/Mycell'
export default {
mounted () {




if(!localStorage.username){
this.$router.push('/login')
return
}
this.checkUser()
},
components: {
Group,
Cell,
CellFormPreview,
CellBox,
Badge,
Mycell,
Alert
},
methods: {
onClick () {
console.log('on click')
},
checkUser:function(){
if(!localStorage.username){
this.show=true
this.message ="请登录"
this.$router.push('/login')
return
}else {
var params = new URLSearchParams()
params.append('username', localStorage.username)
var that = this
this.$http.post('user/getUserInfo',params)
.then((ret)=>{
that.username=ret.data.re.username

if (ret.data.re.super==0) {
that.usertype='代理'
}
if (ret.data.re.super==1) {
that.usertype='超管'
}
that.roomcardnum=ret.data.re.roomcard
})
}

},
logout() {
localStorage.removeItem('username');
this.$http.post('/logout')
.then((ret)=>{
this.$router.push('/login')
})

}
},
data () {
return {
list: [{
label: 'Apple',
value: '3.29'
}, {
label: 'Banana',
value: '1.04'
}, {
label: 'Fish',
value: '8.00'
}],
show:false,
title:'',
message:'',
money: null,
showContent001: false,
showContent002: false,
showContent003: false,
showContent004: false,
username:'',
usertype:'',
roomcardnum:''

}
}
}



