
import Alert from 'vux/src/components/alert/index.vue'

import md5 from 'js-md5';
export default {
data(){
return {
username: '',
password: '',
message: '',
show: false,
title: '',
}
},
components:{
Alert
},
methods:{
jumptomain:function(){
this.$router.push('/')
},
jumptoforget:function(){
this.show=true
this.message='请联系管理员进行修改'
},
jumptoregister:function(){
this.$router.push('/register')
},
login:function(){
var params = new URLSearchParams()
params.append('username',this.username)
params.append('password',md5(this.password))
var that = this
this.$http.post('login',params)
.then((res)=>{
if(res.data.err==0){
that.show=true
that.message ="登录成功！"
localStorage.username=that.username
that.$router.push('/')

}
if(res.data.err==-1){
that.show=true
that.message ="用户名或密码错误"
}
})
}
}
}



