
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'
import Alert from 'vux/src/components/alert/index.vue'

export default {
data(){
return {
cardnum:'',
title:'',
show:false,
message:'',
try: false
}
},
components:{
XInput,
XButton,
Alert,
Group
},
methods:{
chargeforplayer:function(){
if(this.try){
return
}
this.try = !this.try
if (parseInt(this.cardnum)<=0) {
this.show=true;
this.message='请输入正确的房卡数'
return
}
if(this.cardnum ==''){
this.show=true;
this.message='请输入要充值的房卡数'
return
}
var params = new URLSearchParams()
params.append('userid', this.$route.params.chargecardnum)
params.append('roomcardnum', this.cardnum)
var that  =this
this.$http.post('user/chargeRoomcard',params)
.then((ret)=>{
that.try = !that.try
if (ret.data.err==-999) {
that.show=true;
that.message='请先登录'
setTimeout(()=>{that.$router.push('/login')},2000)
return
}
if (ret.data.err==0) {
that.show=true;
that.message='用户房卡充值完成'
setTimeout(()=>{that.$router.push('/')},2000)
return
}
if (ret.data.err==-1) {
that.show=true;
that.message='用户id错误'
setTimeout(()=>{that.$router.push('/playercharge')},1000)
return
}
if (ret.data.err==-2) {
that.show=true;
that.message='用户房卡充值失败'
return
}
if (ret.data.err==-3) {
that.show=true;
that.message='您的剩余房卡数不足，请及时充值'
return
}
})
}
}
}



