
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'
import Alert from 'vux/src/components/alert/index.vue'

export default {
data(){
return {
account:'',
title:'',
show:false,
message:''
}
},
components:{
XInput,
XButton,
Alert,
Group
},
methods:{
chargeforplayer:function(){
if(this.account ==''){
this.show=true;
this.message='请输入要修改的代理手机号'
return
}
var params = new URLSearchParams()
params.append('username', this.account)
console.log(this.account);
this.$http.post('user/resetpassword',params)
.then((ret)=>{
if(ret.data.err==-999){
this.show=true;
this.message='请先登录'
setTimeout(()=>{this.$router.push('/login')},2000)
return
}
if(ret.data.err==0){
this.show=true;
this.message='密码已被重置为admin'
return
}
if(ret.data.err==-1){
this.show=true;
this.message='手机号输入不正确'
return
}
if(ret.data.err==-2){
this.show=true;
this.message='密码重置失败'
return
}
if(ret.data.err==-3){
this.show=true;
this.message='您不是超管哦！'
return
}
})
}
}
}



